// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";

// Brand-top

.brand-top {
  font-family: 'Stolzl';
  line-height: 150%;
  color: #0F1A34;
  padding-top: 10px 0;
  background-color: #CBDCFF;
  @media (max-width: 576px) {
    font-size: 12px;
    padding-top: 5px;
    padding-bottom: 5px;
    img {
      width: 20px;
      height: 20px;
    }
  }
}

// header

.header {

  // .header__top

  &__top {
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #E0E3E3;
    @media (max-width: 992px) {
      flex-wrap: wrap;
      row-gap: 20px;
    }
    @media (max-width: 576px) {
      padding: 10px 0;
    }
    @media (max-width: 576px) {
      row-gap: 10px;
    }
  }

  // .header__logo

  &__logo {
    @media (max-width: 576px) {
      img {
        max-width: 120px;
      }
    }
  }

  // .header__wrap

  &__wrap {
    display: flex;
    position: relative;
    column-gap: 24px;
    form {
      input {
        font-family: 'Stolzl';
        font-weight: 400;
        font-style: normal;
        font-size: 12px;
        line-height: 150%;
        width: 640px;
        padding: 11px 50px;
        border: 1.6px solid #E0E3E3;
        border-radius: 4px;
        position: relative;
        @media (max-width: 1400px) {
          width: 500px;
        }
        @media (max-width: 1200px) {
          width: 300px;
        }
        @media (max-width: 992px) {
          width: 100%;
        }
        @media (max-width: 576px) {
          padding: 7px 20px 7px 30px;
        }
      }
    }
    @media (max-width: 992px) {
      order: 2;
      width: 100%;
      display: grid;
      grid-template-columns: 4fr 135px;
    }
    @media (max-width: 480px) {
      grid-template-columns: 1fr;
      row-gap: 10px;
    }
  }

  // .header__btn

  &__btn {
    font-family: 'Stolzl';
    font-size: 12px;
    line-height: 150%;
    color: #FFFFFF;
    background: #2D3132;
    border-radius: 4px;
    padding: 10px 20px;
    transition: all 0.3s ease 0s;
    &:hover {
      box-shadow: 0 0 20px rgba(0,0,0,0.3);
    }
    @media (max-width: 576px) {
      padding-top: 7px;
      padding-bottom: 7px;
      font-size: 10px;
      line-height: 130%;
    }
    @media (max-width: 480px) {
      width: 100%;
    }
  }

  // .header__lang

  &__lang {
    margin-left: 24px;
    select {
      border: none;
      font-family: 'Stolzl';
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 150%;
      color: var(--primaryColor);
    }
    @media (max-width: 480px) {
      display: none;
    }
  }

  // .header__info

  &__info {
    display: flex;
    margin-left: 24px;
    column-gap: 20px;
    @media (max-width: 576px) {
      margin-left: 0;
      column-gap: 10px;
    }
  }

  // .header__favorite

  &__favorite {
    position: relative;
    transition: all 0.3s ease 0s;
    &:hover {
      transform: translateY(-3px);
    }
    > span {
      position: absolute;
      top: -1px;
      right: -3px;
      width: 14px;
      height: 14px;
      font-family: 'Stolzl';
      font-style: normal;
      font-weight: 400;
      font-size: 10px;
      line-height: 173%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      color: var(--primaryColor);
      background: #EFF1F1;
      border: 2px solid #FFffff;
      border-radius: 13px;
    }
  }

  // .header__message

  &__message {
    position: relative;
    transition: all 0.3s ease 0s;
    &:hover {
      transform: translateY(-3px);
    }
    > span {
      position: absolute;
      top: -1px;
      right: -3px;
      width: 14px;
      height: 14px;
      font-family: 'Stolzl';
      font-style: normal;
      font-weight: 400;
      font-size: 10px;
      line-height: 173%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #ffffff;
      background: #FF5449;
      border: 2px solid #FFFFFF;
      border-radius: 13px;
    }
  }

  // .header__cart

  &__cart {
    position: relative;
    transition: all 0.3s ease 0s;
    &:hover {
      transform: translateY(-3px);
    }
    > span {
      position: absolute;
      top: -1px;
      right: -3px;
      width: 14px;
      height: 14px;
      font-family: 'Stolzl';
      font-style: normal;
      font-weight: 400;
      font-size: 10px;
      line-height: 173%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      color: var(--primaryColor);
      background: #EFF1F1;
      border: 2px solid #FFffff;
      border-radius: 13px;
    }
  }

  // .header__login

  &__login {
    padding-right: 20px;
    margin-left: 24px;
    background-image: url('../img/s-bottom.svg');
    background-position: 100% center;
    background-size: 12px 12px;
    background-repeat: no-repeat;
    cursor: pointer;
    @media (max-width: 576px) {
      margin-left: 0;
    }
  }

  .btn-search {
    width: 20px;
    height: 20px;
    border: none;
    outline: none;
    background-color: transparent;
    position: absolute;
    left: 16px;
    top: 50%;
    transform: translateY(-50%);
    background-image: url('../img/search.svg');
    background-repeat: no-repeat;
    background-size: 20px 20px;
    @media (max-width: 576px) {
      background-size: 15px 15px;
      left: 10px;
      top: 20px;
    };
  }

  .container-fluid {
    @media (min-width: 992px) {
      padding-left: 0 !important;
    }
    @media (max-width: 992px) {
      padding-right: 0 !important;
      padding-left: 0 !important;
    }
  }
  .navbar-toggler {
    margin-left: auto;
    box-shadow: none !important;
    @media (max-width: 576px) {
      padding: 2px !important;
    }
  }

  .navbar-collapse {
    @media (max-width: 992px) {
      margin-top: 15px;
      //background-color: rgb(250, 248, 248);
      box-shadow: 0 0 5px rgba(0,0,0,.3);
    }
  }

  .nav-link {
    font-family: 'Stolzl';
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 150%;
    letter-spacing: 0.4px;
    padding: 8px 16px !important;
    text-transform: uppercase;
    color: #444748 !important;
    transition: all 0.3s ease 0s;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      width: 0%;
      height: 2px;
      background-color: var(--primaryColor);
      left: 0;
      bottom: -8px;
      transition: all 0.3s ease 0s;
    }
    &:hover {
      &:after {
        width: 100%;
      }
    }
    @media (max-width: 1200px) {
      padding-left: 10px !important;
      padding-right: 10px !important;
    }
    @media (max-width: 992px) {
      &:after {
        content: none;
      }
    }
  }
}

// Catalog

.catalog {
  .container {
    @media (max-width: 576px) {
      padding-right: 0;
      padding-left: 0;
    }
  }
  // Breadcramb

  .breadcrumb {
    margin-top: 32px;
    //align-items: center !important;
  }
  .breadcrumb-item {
    > a {
      font-family: 'Stolzl';
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 150%;
      color: var(--primaryColor);

    }
  }
  .active {
      font-family: 'Stolzl';
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 150%;
      padding-top: 6px;
      color: #747778;
  }

  // .catalog__title

  &__title {
    margin-top: 10px;
    margin-bottom: 20px;
    font-family: 'Stolzl';
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 150%;
    color: var(--primaryColor);
  }

  // Catalog filter

.filter {

		// .filter__title

		&__title {
      margin-bottom: 25px;
      font-family: 'Stolzl';
      font-style: normal;
      font-weight: 400;
      font-size: 22px;
      line-height: 150%;
      color: #5C5F60;
    }

		// .filter__accordion

    .accordion-item {
      border-radius: 0;
      border: none;
      outline: none;
      margin-bottom: 2px;
      &:last-child {
        margin-bottom: 0;
      }
    }

    .accordion-button {
      background-color: #EFF1F1;
      box-shadow: none;
      font-family: 'Stolzl';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: #444748;
    }

    .accordion-body {
      form {
        .form__item {
          width: 100%;
          margin-bottom: 12px;
          &:last-child {
            margin-bottom: 0;
          }
         label {
          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          cursor: pointer;
         }
         .checkbox {
          display: none;
         }
         .fake {
          display: inline-block;
          width: 16px;
          height: 16px;
          border: 1px solid #C4C7C7;
          border-radius: 3px;
          position: relative;
          margin-bottom: -3px;
          margin-right: 10px;
         }
         .fake:before {
          content: '';
          position: absolute;
          left: 4px;
          top: 0px;
          display: block;
          width: 6px;
          height: 10px;
          transform: rotate(45deg);
          border: 2px solid #ffffff;
          border-top: 0;
          border-left: 0;
          opacity: 0;
          transition: all 0.3s ease 0s;
         }
         .checkbox:checked + .fake:before {
            opacity: 1;
         }
         .checkbox:checked + .fake {
            background-color: var(--primaryColor);
        }
        .text {
          font-family: 'Stolzl';
          font-style: normal;
          font-weight: 400;
          font-size: 14px;
          line-height: 150%;
          color: #444748;
        }
        }
        .amount {
          font-family: 'Stolzl';
          font-style: normal;
          font-weight: 400;
          font-size: 14px;
          line-height: 150%;
          color: #747778;
        }
      }
    }

    .accordion-button::after {
      background-image: url('../img/ss-bottom.svg');
      background-size: 20px 20px;
      background-repeat: no-repeat;
      background-position: center;
      @media (max-width: 576px) {
        background-size: 12px 12px;
      }
    }
    .accordion-button:not(.collapsed)::after {
      transform: rotate(-180deg);
    }
    // Range slider

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }

    /* Firefox */
    input[type=number] {
      -moz-appearance: textfield;
    }

    .filter-price {

      // .filter-price__slider

      &__slider {
        margin-bottom: 25px;

      }

      // .filter-price__inputs

      &__inputs {
        display: flex;
        justify-content: space-between;
        column-gap: 24px;
      }

      // .filter-price__label

      &__label {
        flex: 0 1 50%;
      }

      // .filter-price__text

      &__text {
        display: block;
        font-family: 'Stolzl';
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        line-height: 150%;
        color: #747778;
      }

      // .filter-price__input

      &__input {
        border: none;
        outline: none;
        padding: 13px 0;
        border-bottom: 1px solid #C4C7C7;
        width: 100%;
        font-family: 'Stolzl';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 150%;
        color: #2D3132;
        &::placeholder {
          font-family: 'Stolzl';
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          line-height: 150%;
          color: #2D3132;
        }
      }
    }
    .noUi-target {
      background-color: #C4C7C7;
      height: 4px;
      border: none;
      box-shadow: none;
    }
    .noUi-connect {
      background-color: #8E9192;
    }
    .noUi-handle:after, .noUi-handle:before {
      display: none;
    }
    .noUi-handle {
      box-shadow: none;
      width: 16px;
      height: 16px;
      background: #5C5F60;
      border-radius: 4px;
      cursor: pointer;
    }
    .noUi-horizontal .noUi-handle {
      right: -12px;
    }
  }
 // .catalog__content

     &__content {}

      .content {
          // .content__sort

          &__sort {
            margin-bottom: 28px;
            text-align: right;
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 150%;
            color: #5C5F60;
            select {
              appearance: none;
              border: none;
              margin-left: 12px;
              padding: 8px 38px 8px 12px;
              border-radius: 100px;
              background-blend-mode: multiply;
              background-color: #EFF1F1;
              background-image: url('../img/select-bottom.svg');
              background-size: 10px 10px;
              background-repeat: no-repeat;
              background-position: calc(100% - 12px) center;
              cursor: pointer;
            }
            @media (max-width: 768px) {
              margin-top: 20px;
              text-align: left;
            }
          }

          // .content__select

          &__select {
            display: inline-block;
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 150%;
            color: #2D3132;
            position: relative;
              &:after {
                content: '';
                position: absolute;
                top: 0;
                right: 30px;
                width: 1px;
                height: 100%;
                background-color: #ffffff;
                z-index: 100;
              }
          }

          // .content__items

          &__items {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            column-gap: 24px;
            row-gap: 24px;
            @media (max-width: 1200px) {
              grid-template-columns: repeat(2, 1fr);
            }
            @media (max-width: 576px) {
              grid-template-columns: 312px;
              justify-content: center;
            }
          }

          // .content__item

          &__item {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 24px;
            background: #FFFFFF;
            border: 1px solid #EFF1F1;
            border-radius: 4px;
            transition: all 0.3s ease 0s;
            cursor: pointer;
            &:hover {
              border-color: #ffffff;;
              box-shadow: 0 0 25px 0 rgba(0,0,0,0.5);
              transform: translateY(-10px);
            }
          }

          // .content__top

          &__top {
            display: flex;
            justify-content: space-between;
            > img {
              width: 20px;
              height: 20px;
            }
          }

          // .content__organic

          &__organic {
            display: flex;
            align-items: center;
            padding: 4px 6px;
            background: #E0E3E3;
            border-radius: 4px;
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 400;
            font-size: 10px;
            line-height: 150%;
            color: var(--primaryColor);

            // .content__organic_hide

            &_hide {
              opacity: 0;
            }
          }

          // .content__img

          &__img {
            text-align: center;
          }

          // .content__up

          &__up {
            background: #B4FFF0;
            border-radius: 4px;
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 400;
            font-size: 10px;
            line-height: 100%;
            color: #00382F;
            max-width: max-content;
            padding: 4px 8px;
            // .content__top_hide

            &_hide {
              opacity: 0;
            }
          }
          // .content__brand

          &__brand {
            margin-top: 16px;
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 500;
            font-size: 10px;
            line-height: 150%;
            color: #5C5F60;
          }

          // .content__name

          &__name {
            margin-top: 4px;
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            color: var(--primaryColor);
          }

          // .content__fotter

          &__fotter {
            margin-top: 24px;
            display: flex;
            justify-content: space-between;
          }

          // .content__prices

          &__prices {}

          // .content__old-price

          &__old-price {
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 150%;
            text-decoration-line: line-through;
            color: #747778;
          }

          // .content__price

          &__price {
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 150%;
            color: var(--primaryColor);

          }

          // .content__info

          &__info {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
          }

          // .content__location

          &__location {
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 150%;
            color: #2D3132;
            padding-left: 14px;
            background-image: url('../img/location.svg');
            background-repeat: no-repeat;
            background-size: 12px 12px;
            background-position: left center;
          }

          // .content__author

          &__author {
            display: flex;

          }

          // .content__avatar-name

          &__avatar-name {
            font-family: 'Stolzl';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            color: var(--primaryColor);
            padding-left: 8px;
          }
          // Pagination

            .paginations {
              margin-top: 56px;
              text-align: center;
              @media (max-width: 768px) {
                  margin-top: 30px;
              }
              .pagination {
                @media (max-width: 480px) {
                  flex-wrap: wrap;
                  row-gap: 10px;
                  justify-content: center;
                }
              }
              .page-item {
                margin-right: 8px;
                &:last-child {
                  margin-right: 0;
                }
                &.active {
                  padding-top: 0;
                  .page-link {
                    border: 1.6px solid #2D3132;
                    padding: 10px 16px;
                    margin-top: -3px;
                    background: transparent;
                    @media (max-width: 768px) {
                      padding: 6px 10px !important;
                    }
                  }
                }
              }
              .page-link {
                border: 1.6px solid #C4C7C7;
                padding: 7px 13px;
                border-radius: 100px;
                font-family: 'Stolzl';
                font-style: normal;
                font-weight: 400;
                font-size: 12px;
                line-height: 150%;
                display: flex;
                align-items: center;
                text-align: center;
                color: #5C5F60;
                @media (max-width: 768px) {
                  padding: 4px 8px !important;
                  font-size: 10px;
                }
              }
            }
      }
}


// Footer

.footer {
  margin-top: 100px;
  padding-top: 50px;
  padding-bottom: 14px;
  background-color: #EFF1F1;
  @media (max-width: 992px) {
    margin-top: 50px;
  }
  @media (max-width: 576px) {
    margin-top: 25px;
    padding-top: 25px;
  }
  ul {
    li {
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
      a {
        font-family: 'Stolzl';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: var(--primaryColor);
        transition: all 0.3s ease 0s;
        &:hover {
          color: #7d8183;
        }
        @media (max-width: 576px) {
          font-size: 12px;
        }
      }
    }
    @media (max-width: 992px) {
      margin-bottom: 20px;
    }
    @media (max-width: 576px) {
      text-align: center;
      li {
        margin-bottom: 5px;
      }
    }
  }
  .copyright {
    margin-top: 50px;
    border-top: 1px solid #C4C7C7;
    padding-top: 30px;
    font-family: 'Stolzl';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: var(--primaryColor);
    a {
      color: var(--primaryColor);
      &:hover {
        text-decoration: underline;
      }
    }
    @media (max-width: 576px) {
      margin-top: 25px;
      padding-top: 14px;
      text-align: center;
      p {
        margin-bottom: 0;
      }
    }
  }
}






